/* 
Set box-sizing to border-box for all elements (easier to calculate widths etc.) 
Also set
*/

:root {
  --almost-black: hsl(0, 0%, 10%);
  --very-blue: hsl(221, 100%, 50%);
  --less-blue: hsl(221, 100%, 85%);
}

html {
  box-sizing: border-box;
  font-size: 100%;
  font-family: sans-serif;
  color: hsl(0, 0%, 10%);
  color: var(--almost-black);
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  max-width: 75em;
  margin: auto;
  background-color: hsl(20, 33%, 98%);
}

.content-wrapper {
  display: -webkit-flex;
  display: flex;
  margin: 1rem;
}

.sidebar {
  -webkit-flex: 1 1;
          flex: 1 1;
  height: 100%;
}

.main-content {
  margin-left: 1rem;
  -webkit-flex: 3 1;
          flex: 3 1;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.card {
  background-color: white;
  border: 1px hsl(0, 0%, 75%) solid;
  border-radius: 3px;
  box-shadow: hsl(0, 0%, 85%) 3px 3px 0px 0px;
  padding: 1rem;
}

.card--secondary {
  background-color: var(--less-blue);
  border: 1px hsl(0, 0%, 75%) solid;
  box-shadow: var(--very-blue) 3px 3px 0px 0px;
}

.card__title {
  margin: 0 0 1rem 0;
  font-weight: normal;
  font-size: 1.4rem;
  text-transform: uppercase;
}

.info-text {
  margin: 1rem 0;
  font-style: italic;
}

.info-text--center {
  text-align: center;
}

@media (max-width: 35em) {
  .content-wrapper {
    -webkit-flex-direction: column-reverse;
            flex-direction: column-reverse;
  }

  .main-content {
    margin-left: 0;
  }
}

.search {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 2rem;
}

.search__label {
  margin-bottom: 0.5rem;
}

.search__text-field {
  display: block;
  width: 100%;
  margin-top: 0.5rem;
  border-radius: 3px;
  border: 1px solid black;
  padding: 5px;
}

.search__btn {
  cursor: pointer;
  background-color: var(--less-blue);
  border: 2px var(--very-blue) solid;
  border-radius: 3px;
  padding: 0.25rem;
  width: 5rem;
}

.city {
  border: 1px black solid;
  border-radius: 3px;
  min-width: 14rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  padding: 1rem;
}

.city__name {
  margin: 0 0 0.5rem 0;
  font-weight: normal;
  text-transform: uppercase;
}

.city__temp {
  font-size: 5rem;
  margin: 1rem;
  font-weight: normal;
}

.city__fav-btn {
  cursor: pointer;
  display: block;
  width: 7.5rem;
  border-radius: 3px;
  background-color: hsl(120, 100%, 25%);
  border: None;
  padding: 0.35rem;
  color: white;
  margin: auto;
}

.city__fav-btn--remove {
  background-color: hsl(0, 100%, 40%);
}

.city__data-disclaimer {
  position: relative;
  bottom: -0.75rem;
  font-size: 0.55rem;
}

.favourites {
  list-style: none;
  padding: 0;
}

.favourites__item {
  cursor: pointer;
  margin: 0.25rem 0;
  padding: 0.25rem;
  text-transform: uppercase;
  font-weight: bold;
}

.favourites__item:hover {
  background-color: hsl(221, 100%, 95%);
}

.favourites__item--selected {
  background-color: hsl(221, 100%, 90%);
}

